.card{
    width: 15rem;
    border-radius: 5%;
  margin-bottom: 3rem;
    text-align: center;
    margin-left:3rem;
    float: left;
    height:25rem;
  }
  .light-mode .card{
    background: #139458;
    color: #fff;
  }
  .dark-mode .card{
    background-color: #1b2938;
    color: #fff;
  }
  
  .card:hover { transform: scale(1.1); }

  .card-img-top{
    width:100%;
    height:80px;
  }
  
  .card> h3>a{
      color:white;
      text-decoration: none;
  }