.image-container
{
    background:url("https://i.ibb.co/wYBkM37/Biryani-rice-Vegetable-biryani-Indian-basmati-rice-curry-vegetables-and-spices-Indian-kitchen.jpg");
    background-repeat: no-repeat;
    width: 100%;
    height: 480px;
    background-size:100% 100%;
}

.location-container{
    text-align: center;
}


#logo
{
    text-align:center;
    color: #eb2929;
    font-weight:bold;
    font-size:70px;
    background:white;
    height:100px;
    width:100px;
    margin: auto;
    border-radius:50%;
    margin-top: 81px; 
}

.heading
{
    color: #ffffff;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
    font-family:Poppins;
    margin-top: 21px;
    line-height: 2;
    margin-bottom: 10px;
}

.locationDropDown{
    height: 40px;
    margin-right: 2%;
    outline: none;
    border-radius: 1rem;
}

@media only screen and (max-width: 600px) {
    .locationDropDown{
        height: 40px;
        font-size: 10px;
        margin-right: 3%;
    }
  }


