
.light-mode .listheading{
    font-size: 36px;
    font-weight: bold;
    color: #192f60;
}
.dark-mode .listheading{
    color: #e4e2e2;
}
.suggest{
    color: #192f60;
}
.dark-mode .suggest{
    color: #e4e2e2;
}

.list{
    height: 27rem;
    margin-top: 5%;
    cursor: pointer;
    border-radius: 5%;
    box-shadow: 6px 10px 15px gray;
    background-color: #e4e2e2;
}
.list:hover { transform: scale(1.1); }
.dark-mode .list{
    background-color: #1b2938;
}

.listimage{
    height: 25rem;
    width: 50%;
    float: left;
    margin: 1%;
    border-radius: 20%;
}
.light-mode .listhotel{
    color: #4b6dbd;
}
.dark-mode .listhotel{
    color: #e4e2e2;
}
.list-option{
    border-top: 1px solid gray;
    margin-top: 2rem;
}
.hotelname{
  font-size: 30px;
  font-weight: 600;
  margin-top: 3%;
  color: #192f60;
  text-decoration: none !important;
}
.dark-mode .hotelname{
    color: rgb(182, 172, 172);
}

.cuisine-cost{
    margin-top: 1rem;  
}
.dark-mode .filterhead{
    color: #e4e2e2;
}
.dark-mode .btn-primary{
    background-color: #e4e2e2;
    color: #191919;
}
.dark-mode .currencyhead{
    color: #e4e2e2;
}

.filter1{
    margin-bottom: 8%;
}
#filter1{
    color: #192f60 !important;
    background-color: white;
    border: white;
    font-size: 2rem;
    
}

.dark-mode #filter1{
    background-color: #191919;
    color: #e4e2e2 !important;
}
#filterbrand{
    font-size: 3rem;
}
.dark-mode #filterbrand{
    color: #e4e2e2 !important;
   
}
#downarrow:hover{
    background-color: #ce0505;
}

@media only screen and (max-width: 600px) {
.listhotel{
    font-size: 11px;
}
  }