body{
  font-family: 'Poppins', sans-serif;
}

.about{
  margin-top: 3%;
  font-size: 22px;
  font-weight: 600;
}
.dark-mode .panel{
  background-color:#191919;
  color: #999;
}
.dark-mode .panel-heading{
  background-color:#191919;
  color: #999;
}

.option{
  font-size: 18px;
  font-weight: 600;
  margin-top: 4%;
}
.dark-mode .option{
  color:#e4e2e2 ; 
}
.data{
  font-size: 16px;
  font-weight: normal;
  color: #ce0505;
}
.dark-mode .data{
 
  color: rgb(182, 172, 172);
}

.mapbody{
  width: 80%;
  height: 100vh;
}
.map{
  width: 80%;
  height: 100vh;
}
.mapbutton{
  background-color: #ce0505;
  color: #fff !important;
  float: left;
  width: 15rem;
  height: 5rem;
  font-size: 2rem;
  outline: none !important;
}
 .btn-lg{
   background-color: #ce0505;
  color: #fff !important;
  float: right;
  width: 18rem;
  height: 5rem;
  outline: none !important;
}
.dark-mode .btn-lg {
  background-color: #1b2938;
  color: #fff !important;
}
.dark-mode .mapbutton{
  background-color: #1b2938;
  color: #fff !important;
}

.head{
  color: white !important;
  font-size: 30px;
  font-weight: bold;
  margin-left: 10%;
}
.modal-content{
  text-align: center;
}
.order{
  margin-left: 7%;
  padding:3% ;
}
.react-tabs__tab-list {
  border-bottom: solid 1px rgb(196, 189, 189);
    margin-left: 10%;
    padding: 1%;
  }
  
  .react-tabs__tab {
    display: inline;
    color:  #192f60;
    font-size: 22px;
    font-weight: 600;
    margin-right: 4%;
    cursor: pointer;
  }
  .dark-mode .react-tabs__tab {
    background-color:#191919;
    color: #e4e2e2;
  }
  
  .react-tabs__tab--selected {
    background:#fff;
    border-bottom: solid 3px #ce0505;
    padding: 1%;
    font-size: 22px;
    font-weight: 600;
    color:#192f60;
  }

.react-tabs__tab--selected:focus{
    outline: none;
}
.react-tabs__tab--selected:focus ::after{
    outline: none;
}

.react-tabs__tab-panel {
    display: none;
    margin-left: 10%;
    color: #192f60;
  }
  .dark-mode .react-tabs__tab-panel {
    color: #e4e2e2;
  }
  
  .react-tabs__tab-panel--selected {
    display: block;
  }